import React from 'react';
import { withRouter } from 'react-router-dom';
import { Link } from 'react-router-dom';
import uuidv4 from 'uuid/v4';
import Highlighter from 'react-highlight-words';
import moment from 'moment';
import {
  Icon,
  Input,
  Breadcrumb,
  Table,
  Tabs,
  Button,
  message,
  Popconfirm,
  Modal,
  Row,
  Col,
  Form,
  Select,
  Radio,
  Tooltip,
  Dropdown,
  Menu,
} from 'antd';
import styled from 'styled-components';
import { injectIntl } from 'react-intl';
import {
  listAllUsers,
  createUser,
  updateUser,
  listTypeOfPermits,
  removeUser,
} from './../../services';
import './index.css';

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

const getClients = users => {
  const clients = [];
  const filterClient = [];
  for (let i = 0; i < users.length; i++) {
    if (users[i].rol === 'client') {
      clients.push(users[i]);
    }

    if (users[i].client) {
      if (!filterClient.length) {
        filterClient.push({
          text: users[i].client === '---' ? 'Without Client' : users[i].client,
          value: users[i].client,
        });
      } else {
        let flag = false;
        for (let j = 0; j < filterClient.length; j++) {
          if (filterClient[j].value === users[i].client) {
            flag = true;
            break;
          }
        }
        if (!flag) {
          filterClient.push({
            text: users[i].client,
            value: users[i].client,
          });
        }
      }
    }
  }
  return { clients, filterClient };
};

class UsersCmp extends React.Component {
  state = {
    searchText: '',
    confirmDirty: false,
    loading: false,
    loadingT: false,
    sendLoading: false,
    visible: false,
    visibleEdit: false,
    defaultTab: 'clients',
    users: [],
    clients: [],
    clientF: [],
    clientS: '',
    typeOfPermits: [],
    defaultTypeOfPermits: null,
    mode: 'add',
    apikey: '',
    role: 'user',
    user: {
      fullName: '',
      rol: 'user',
      email: '',
      typeOfUser: 3,
    },
  };

  componentDidMount = () => {
    const { userLogin } = this.props;
    if (userLogin.rol === 'client') {
      this.setState({
        defaultTab: 'all',
      });
    }
    this.getUsers();
    this.getTypeOfPermits();
    this.initialAPIKey();
  };

  initialAPIKey = () => {
    this.setState({ apikey: uuidv4() });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  showModal = (mode, user = null) => {
    const { userLogin } = this.props;
    const { typeOfPermits } = this.state;
    this.setState({
      mode,
    });

    if (mode === 'edit') {
      let top = user.TypeOfPermit ? user.TypeOfPermit.id : null;
      if (!top) {
        top = typeOfPermits.length ? typeOfPermits[0].name : null;
      } else {
        if (typeOfPermits.length) {
          top = typeOfPermits.filter(item => {
            return item.id === top;
          });
          if (top.length) {
            top = top[0].name;
          } else {
            top = typeOfPermits[0].name;
          }
        } else {
          top = null;
        }
      }

      this.setState({
        visibleEdit: true,
        user: user,
        apikey: user.apikey,
        defaultTypeOfPermits: top,
      });
    } else {
      if (userLogin.rol === 'client') {
        let top = userLogin.TypeOfPermit ? userLogin.TypeOfPermit.id : null;
        if (!top) {
          top = typeOfPermits.length ? typeOfPermits[0].name : null;
        } else {
          if (typeOfPermits.length) {
            top = typeOfPermits.filter(item => {
              return item.id === top;
            });
            if (top.length) {
              top = top[0].name;
            } else {
              top = typeOfPermits[0].name;
            }
          } else {
            top = null;
          }
        }

        this.setState({
          visible: true,
          defaultTypeOfPermits: top,
          user: { fullName: '', rol: 'user', email: '', typeOfUser: userLogin.typeOfUser },
        });
      } else {
        this.setState({
          visible: true,
          user: { fullName: '', rol: 'user', email: '', typeOfUser: 3 },
        });
      }
    }
  };

  hideModal = () => {
    this.setState({
      visible: false,
      visibleEdit: false,
    });
    this.handleReset();
    setTimeout(() => {
      this.setState({
        mode: 'add',
      });
    }, 1000);
  };

  getUsers = async () => {
    try {
      this.setState({
        loadingT: true,
      });
      const response = await listAllUsers();
      this.setState({
        users: response.data.users,
        loadingT: false,
      });
    } catch (err) {
      message.error(err.message);
      this.setState({
        loadingT: false,
      });
    }
  };

  getTypeOfPermits = async () => {
    try {
      this.setState({
        loading: true,
      });
      const response = await listTypeOfPermits();
      let defaultTypeOfPermits = null;

      if (this.props.userLogin.rol !== 'client') {
        for (let index = 0; index < response.data.length; index++) {
          if (response.data[index].default) {
            defaultTypeOfPermits = response.data[index].id;
            break;
          }
        }

        this.setState({
          typeOfPermits: response.data,
          defaultTypeOfPermits: defaultTypeOfPermits,
          loading: false,
        });
      } else {
        this.setState({
          typeOfPermits: response.data,
          loading: false,
        });
      }
    } catch (err) {
      message.error(err.message);
      this.setState({
        loading: false,
      });
    }
  };

  handleConfirmBlur = e => {
    const value = e.target.value;
    this.setState({
      confirmDirty: this.state.confirmDirty || !!value,
    });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const {
      form,
      intl: { messages },
    } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  handleSubmit = e => {
    e.preventDefault();
    const {
      form,
      intl: { messages },
    } = this.props;
    const { mode, user, role } = this.state;

    form.validateFields((err, values) => {
      if (!err || (mode === 'edit' && err.confirm && err.password)) {
        const form = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: role,
          typeOfPermits: values.typeOfPermits,
          typeOfUser: values.typeOfUser,
          apikey: values.apikey,
        };

        if (role === 'user' && values.client) {
          form.client = values.client;
        }

        this.setState({
          sendLoading: true,
        });

        if (mode === 'add') {
          createUser(form)
            .then(() => {
              message.success(messages.default.success);

              this.setState({
                sendLoading: false,
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response.status === 400) {
                if (error.response.data.error === 'Max Users Restriction') {
                  message.error('Max Users Restriction');
                } else {
                  message.error(messages.signup.error.email);
                }
              } else {
                message.error(messages.signup.error.all);
              }
              this.setState({
                sendLoading: false,
              });
            });
        } else {
          updateUser(form)
            .then(() => {
              message.success(messages.default.success);
              this.setState({
                sendLoading: false,
              });
              this.handleReset();
              this.getUsers();
              this.hideModal();
            })
            .catch(error => {
              if (error.response && error.response.status === 400) {
                if (error.response.data.error === 'Max Users Restriction') {
                  message.error('Max Users Restriction');
                } else {
                  message.error(messages.signup.error.email);
                }
              } else {
                message.error(messages.signup.error.all);
              }
              this.setState({
                sendLoading: false,
              });
            });
        }
      }
    });
  };

  confirmRemoveUser = async id => {
    const {
      intl: { messages },
    } = this.props;
    try {
      await removeUser({ id });
      this.getUsers();
      message.success(messages.default.success);
    } catch (err) {
      message.error(err.message);
    }
  };

  showDashboard = user => {
    const { history } = this.props;
    history.push(`/dashboard/users/${user.rol}/${user.id}`);
  };

  onChange = activeKey => {
    this.setState({ defaultTab: activeKey });
  };

  getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Button
          type="primary"
          onClick={() => this.handleSearch(selectedKeys, confirm)}
          icon="search"
          size="small"
          style={{ width: 90, marginRight: 8 }}
        >
          Search
        </Button>
        <Button
          onClick={() => this.handleResetSearch(clearFilters)}
          size="small"
          style={{ width: 90 }}
        >
          Reset
        </Button>
      </div>
    ),
    filterIcon: filtered => (
      <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        .toString()
        .toLowerCase()
        .includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => this.searchInput.select());
      }
    },
    render: text => (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[this.state.searchText]}
        autoEscape
        textToHighlight={text.toString()}
      />
    ),
  });

  handleSearch = (selectedKeys, confirm) => {
    confirm();
    this.setState({ searchText: selectedKeys[0] });
  };

  handleResetSearch = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };

  onSelectChange = value => {
    const { clients } = getClients(this.state.users);
    let cl = clients.filter(item => item.id === value);
    if (cl && cl.length) {
      let user = this.state.user;
      user.typeOfUser = cl[0].typeOfUser;
      this.setState({
        defaultTypeOfPermits: cl[0].TypeOfPermit.id,
        user,
      });
    }
  };

  onBlur = () => {
    // console.log('blur');
  };

  onFocus = () => {
    // console.log('focus');
  };

  onSearch = val => {
    // console.log('search:', val);
  };

  handleUserChange = value => {
    this.setState({
      role: value,
    });
  };

  render() {
    const {
      userLogin,
      form: { getFieldDecorator },
      intl: { messages },
    } = this.props;
    const {
      loading,
      loadingT,
      users,
      visible,
      sendLoading,
      user,
      mode,
      defaultTypeOfPermits,
      typeOfPermits,
      visibleEdit,
      clientF,
      defaultTab,
      role,
    } = this.state;

    const columns = [
      {
        title: messages.signup.fullName,
        dataIndex: 'fullName',
        key: 'fullName',
        sorter: (a, b) => a.fullName.length - b.fullName.length,
        sortDirections: ['descend'],
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
      },
      {
        title: 'APIKey',
        dataIndex: 'apikey',
        key: 'apikey',
      },
      {
        title: messages.signup.role,
        dataIndex: 'rol',
        key: 'rol',
      },
      {
        title: messages.default.actions,
        key: 'action',
        width: 190,
        fixed: 'right',
        render: (text, record) => (
          <Row type="flex" justify="end">
            <Tooltip placement="top" title="Stats">
              <Link to={`/dashboard/stats/${record.id}`}>
                <ButtonAction size="small" type="info" shape="circle" icon="line-chart" />
              </Link>
            </Tooltip>
            <Tooltip placement="top" title="Engines">
              <Link to={`/dashboard/engines/user/${record.id}`}>
                <ButtonAction size="small" type="info" shape="circle" icon="tool" />
              </Link>
            </Tooltip>
            <ButtonAction
              onClick={() => {
                this.showDashboard(record);
              }}
              size="small"
              type="info"
              shape="circle"
              icon="dashboard"
            />
            <ButtonAction
              onClick={() => {
                this.showModal('edit', record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
            <Popconfirm
              onConfirm={() => {
                this.confirmRemoveUser(record.id);
              }}
              title={messages.users.confirmDelete}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
            </Popconfirm>
          </Row>
        ),
      },
    ];

    const { clients } = getClients(users);

    const ccolumns = [
      {
        title: messages.signup.fullName,
        dataIndex: 'fullName',
        key: 'fullName',
        sorter: (a, b) => a.fullName.length - b.fullName.length,
        sortDirections: ['descend'],
        ...this.getColumnSearchProps('fullName'),
      },
      {
        filteredValue: clientF || null,
        title: messages.process.client,
        dataIndex: 'client',
        key: 'client',
        ...this.getColumnSearchProps('client'),
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        ...this.getColumnSearchProps('email'),
      },
      {
        title: 'APIKey',
        dataIndex: 'apikey',
        key: 'apikey',
        ...this.getColumnSearchProps('apikey'),
      },
      {
        title: messages.signup.role,
        dataIndex: 'rol',
        key: 'rol',
        filters: [
          {
            text: 'Admin',
            value: 'admin',
          },
          {
            text: 'Client',
            value: 'client',
          },
          {
            text: 'User',
            value: 'user',
          },
        ],
        onFilter: (value, record) => record.rol.indexOf(value) === 0,
      },
      {
        title: 'Last Login',
        dataIndex: 'lastLogin',
        key: 'lastLogin',
        render: text => {
          if (text) {
            var date = moment.utc(text).format('YYYY-MM-DD HH:mm:ss');
            var stillUtc = moment.utc(date).toDate();
            var local = moment(stillUtc).local();

            return local.format('YYYY-MM-DD HH:mm');
          }
        },
      },
      {
        key: 'action',
        width: 190,
        fixed: 'right',
        render: (text, record) => (
          <Row type="flex" justify="end">
            <Tooltip placement="top" title="Stats">
              <Link to={`/dashboard/stats/${record.id}`}>
                <ButtonAction size="small" type="info" shape="circle" icon="line-chart" />
              </Link>
            </Tooltip>
            {record.rol === 'client' ||
              (record.rol === 'admin' && record.id === userLogin.id && (
                <Tooltip placement="top" title="Engines">
                  <Link to={`/dashboard/engines/user/${record.id}`}>
                    <ButtonAction size="small" type="info" shape="circle" icon="tool" />
                  </Link>
                </Tooltip>
              ))}
            <ButtonAction
              onClick={() => {
                this.showDashboard(record);
              }}
              size="small"
              type="info"
              shape="circle"
              icon="dashboard"
            />

            {record.rol !== 'admin' && (
              <React.Fragment>
                <ButtonAction
                  onClick={() => {
                    this.showModal('edit', record);
                  }}
                  size="small"
                  type="primary"
                  shape="circle"
                  icon="edit"
                />
                <Popconfirm
                  onConfirm={() => {
                    this.confirmRemoveUser(record.id);
                  }}
                  title={messages.users.confirmDelete}
                  okText={messages.default.yes}
                  cancelText={messages.default.no}
                >
                  <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
                </Popconfirm>
              </React.Fragment>
            )}
          </Row>
        ),
      },
    ];
    const clcolumns = [
      {
        title: messages.signup.fullName,
        dataIndex: 'fullName',
        key: 'fullName',
        sorter: (a, b) => a.fullName.length - b.fullName.length,
        sortDirections: ['descend'],
        ...this.getColumnSearchProps('fullName'),
      },
      {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        ...this.getColumnSearchProps('email'),
      },
      {
        title: 'APIKey',
        dataIndex: 'apikey',
        key: 'apikey',
        ...this.getColumnSearchProps('apikey'),
      },
      {
        title: 'Last Login',
        dataIndex: 'lastLogin',
        key: 'lastLogin',
        render: text => {
          if (text) {
            var date = moment.utc(text).format('YYYY-MM-DD HH:mm:ss');
            var stillUtc = moment.utc(date).toDate();
            var local = moment(stillUtc).local();

            return local.format('YYYY-MM-DD HH:mm');
          }
        },
      },
      {
        title: 'Subscription',
        dataIndex: 'hasSubscription',
        key: 'hasSubscription',
        render: text => {
          if (text) {
            return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
          }
          return <Icon type="close-circle" theme="twoTone" twoToneColor="#eb2f96" />;
        },
      },
      {
        key: 'action',
        render: (text, record) => {
          const menu = (
            <Menu>
              <Menu.Item key="0">
                <Link to={`/dashboard/stats/${record.id}`}>
                  <Icon type="line-chart" />
                  &nbsp;&nbsp;Stats
                </Link>
              </Menu.Item>

              {record.rol === 'client' && (
                <Menu.Item key="1">
                  <Link to={`/dashboard/engines/user/${record.id}`}>
                    <Icon type="tool" />
                    &nbsp;&nbsp;Engines
                  </Link>
                </Menu.Item>
              )}
              {record.rol === 'client' && (
                <Menu.Item key="2">
                  <Link to={`/dashboard/subscriptions/user/${record.id}`}>
                    <Icon type="bell" />
                    &nbsp;&nbsp;Subscriptions
                  </Link>
                </Menu.Item>
              )}
              <Menu.Item
                key="3"
                onClick={() => {
                  this.showDashboard(record);
                }}
              >
                <Icon type="dashboard" />
                &nbsp;Dashboard
              </Menu.Item>

              <Menu.Item
                key="4"
                onClick={() => {
                  clientF.push(record.email);
                  this.setState({
                    clientF,
                    defaultTab: 'all',
                  });
                }}
              >
                <Icon type="user" />
                &nbsp;Users
              </Menu.Item>
              <Menu.Item
                key="5"
                onClick={() => {
                  this.showModal('edit', record);
                }}
              >
                <Icon type="edit" />
                &nbsp;Edit
              </Menu.Item>
            </Menu>
          );
          return (
            <Dropdown overlay={menu} trigger={['click']}>
              <a className="ant-dropdown-link">
                {messages.default.actions} <Icon type="down" />
              </a>
            </Dropdown>
          );

          // return (
          //   <Row type="flex" justify="end">
          //     <Tooltip placement="top" title="Stats">
          //       <Link to={`/dashboard/stats/${record.id}`}>
          //         <ButtonAction size="small" type="info" shape="circle" icon="line-chart" />
          //       </Link>
          //     </Tooltip>
          //     {record.rol === 'client' && (
          //       <Tooltip placement="top" title="Engines">
          //         <Link to={`/dashboard/engines/user/${record.id}`}>
          //           <ButtonAction size="small" type="info" shape="circle" icon="tool" />
          //         </Link>
          //       </Tooltip>
          //     )}
          //     <ButtonAction
          //       onClick={() => {
          //         this.showDashboard(record);
          //       }}
          //       size="small"
          //       type="info"
          //       shape="circle"
          //       icon="dashboard"
          //     />
          //     <Tooltip placement="top" title="Users">
          //       <ButtonAction
          //         onClick={() => {
          //           clientF.push(record.email);
          //           this.setState({
          //             clientF,
          //             defaultTab: 'all',
          //           });
          //         }}
          //         size="small"
          //         type="info"
          //         shape="circle"
          //         icon="user"
          //       />
          //     </Tooltip>

          //     <ButtonAction
          //       onClick={() => {
          //         this.showModal('edit', record);
          //       }}
          //       size="small"
          //       type="primary"
          //       shape="circle"
          //       icon="edit"
          //     />
          //     <Popconfirm
          //       onConfirm={() => {
          //         this.confirmRemoveUser(record.id);
          //       }}
          //       title={messages.users.confirmDelete}
          //       okText={messages.default.yes}
          //       cancelText={messages.default.no}
          //     >
          //       <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
          //     </Popconfirm>
          //   </Row>
          // );
        },
      },
    ];

    const operations = (
      <Button
        loading={loading || loadingT}
        onClick={() => {
          this.showModal('add');
        }}
        type="primary"
        icon="user-add"
      >
        {messages.users.addUser}
      </Button>
    );

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };

    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };

    let showPass = (
      <div>
        <FormItem label={messages.signup.password} hasFeedback>
          {getFieldDecorator('password', {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.required,
              },
              {
                validator: this.validateToNextPassword,
              },
            ],
          })(<Input autoComplete="new-password" disabled={mode === 'see'} type="password" />)}
        </FormItem>
        <FormItem label={messages.signup.confirm} hasFeedback>
          {getFieldDecorator('confirm', {
            rules: [
              {
                required: true,
                message: messages.signup.rules.password.confirm,
              },
              {
                validator: this.compareToFirstPassword,
              },
            ],
          })(<Input autoComplete="new-password" type="password" onBlur={this.handleConfirmBlur} />)}
        </FormItem>
      </div>
    );

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>{messages.menus.user}</Breadcrumb.Item>
        </Breadcrumb>
        <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
          <Tabs activeKey={defaultTab} tabBarExtraContent={operations} onChange={this.onChange}>
            {userLogin.rol === 'admin' && (
              <TabPane tab={messages.users.clients} key="clients">
                <Table
                  size="small"
                  loading={loading || loadingT}
                  dataSource={clients}
                  columns={clcolumns}
                  scroll={{ x: 1200 }}
                  rowKey={record => record.id}
                />
              </TabPane>
            )}
            <TabPane tab={messages.users.all} key="all">
              {userLogin.rol === 'admin' ? (
                <Table
                  size="small"
                  loading={loading || loadingT}
                  dataSource={users}
                  columns={ccolumns}
                  scroll={{ x: 1200 }}
                  rowKey={record => record.id}
                />
              ) : (
                <Table
                  size="small"
                  loading={loading || loadingT}
                  dataSource={users}
                  columns={columns}
                  scroll={{ x: 900 }}
                  rowKey={record => record.id}
                />
              )}
            </TabPane>
          </Tabs>
          <Modal
            centered
            width={600}
            title={messages.users.create}
            visible={visible}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                {messages.default.cancel}
              </Button>,
              <Button key="submit" type="primary" loading={sendLoading} onClick={this.handleSubmit}>
                {messages.default.create}
              </Button>,
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit} autocomplete="off">
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label={messages.signup.fullName} hasFeedback>
                    {getFieldDecorator('fullName', {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: messages.signup.rules.fullName.required,
                        },
                      ],
                    })(<Input disabled={mode === 'see'} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator('email', {
                      initialValue: user.email,
                      rules: [
                        {
                          type: 'email',
                          message: messages.signup.rules.email.type,
                        },
                        {
                          required: true,
                          message: messages.signup.rules.email.required,
                        },
                      ],
                    })(<Input disabled={mode !== 'add'} />)}
                  </FormItem>
                  <FormItem label="APIkey">
                    {getFieldDecorator('apikey', {
                      initialValue: this.state.apikey,
                    })(
                      <Input
                        addonAfter={
                          <Icon
                            onClick={() => {
                              this.setState({ apikey: uuidv4() });
                            }}
                            style={{ cursor: 'pointer' }}
                            type="reload"
                          />
                        }
                      />
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.role} hasFeedback>
                    <Select
                      defaultValue={role}
                      disabled={userLogin.rol === 'client'}
                      onChange={this.handleUserChange}
                    >
                      <Option value="user">{messages.signup.roles.user}</Option>
                      <Option value="client">{messages.signup.roles.client}</Option>
                      <Option value="admin">{messages.signup.roles.admin}</Option>
                    </Select>
                  </FormItem>
                  {role === 'user' && userLogin.rol === 'admin' && (
                    <FormItem label={messages.signup.roles.client} hasFeedback>
                      {getFieldDecorator('client', {
                        rules: [
                          {
                            required: true,
                          },
                        ],
                      })(
                        <Select
                          showSearch
                          placeholder="Select a client"
                          optionFilterProp="children"
                          onChange={this.onSelectChange}
                          onFocus={this.onFocus}
                          onBlur={this.onBlur}
                          onSearch={this.onSearch}
                          filterOption={(input, option) =>
                            option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                          }
                        >
                          {clients.map(item => {
                            return (
                              <Option
                                key={item.id}
                                value={item.id}
                              >{`${item.email} / ${item.apikey}`}</Option>
                            );
                          })}
                        </Select>
                      )}
                    </FormItem>
                  )}
                  <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                    {getFieldDecorator('typeOfPermits', {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <Select disabled={userLogin.rol === 'client'}>
                        {typeOfPermits.map(item => {
                          return (
                            <Option key={item.id} value={item.id}>
                              {item.name}
                            </Option>
                          );
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfUser}>
                    {getFieldDecorator('typeOfUser', {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <RadioGroup disabled={userLogin.rol === 'client'}>
                        <Radio style={radioStyle} value={1}>
                          {messages.signup.typeOfUsers.nqnp}
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          {messages.signup.typeOfUsers.qnp}
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          {messages.signup.typeOfUsers.qp}
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>

                  {showPass}
                </Col>
              </Row>
            </Form>
          </Modal>
          <Modal
            centered
            width={600}
            title={messages.users.edit}
            visible={visibleEdit}
            onOk={this.hideModal}
            onCancel={this.hideModal}
            footer={[
              <Button key="back" onClick={this.hideModal}>
                {messages.default.cancel}
              </Button>,
              <Button key="submit" type="primary" loading={sendLoading} onClick={this.handleSubmit}>
                {messages.default.save}
              </Button>,
            ]}
          >
            <Form {...formItemLayout} onSubmit={this.handleSubmit} autocomplete="off">
              <Row justify="center" type="flex">
                <Col xs={24}>
                  <FormItem label={messages.signup.fullName} hasFeedback>
                    {getFieldDecorator('fullName', {
                      initialValue: user.fullName,
                      rules: [
                        {
                          required: true,
                          message: messages.signup.rules.fullName.required,
                        },
                      ],
                    })(<Input disabled={mode === 'see'} type="text" />)}
                  </FormItem>
                  <FormItem label="E-mail" hasFeedback>
                    {getFieldDecorator('email', {
                      initialValue: user.email,
                      rules: [
                        {
                          type: 'email',
                          message: messages.signup.rules.email.type,
                        },
                        {
                          required: true,
                          message: messages.signup.rules.email.required,
                        },
                      ],
                    })(<Input disabled={mode !== 'add'} />)}
                  </FormItem>
                  <FormItem label="APIkey">
                    {getFieldDecorator('apikey', {
                      initialValue: this.state.apikey,
                    })(<Input disabled />)}
                  </FormItem>
                  <FormItem label={messages.signup.role} hasFeedback>
                    {getFieldDecorator('rol', {
                      initialValue: user.rol,
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <Select disabled={userLogin.rol === 'client'}>
                        <Option value="user">{messages.signup.roles.user}</Option>
                        <Option value="client">{messages.signup.roles.client}</Option>
                        <Option value="admin">{messages.signup.roles.admin}</Option>
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfPermits} hasFeedback>
                    {getFieldDecorator('typeOfPermits', {
                      initialValue: defaultTypeOfPermits,
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <Select disabled={userLogin.rol === 'client'}>
                        {typeOfPermits.map(item => {
                          return <Option key={item.id}>{item.name}</Option>;
                        })}
                      </Select>
                    )}
                  </FormItem>
                  <FormItem label={messages.signup.typeOfUser}>
                    {getFieldDecorator('typeOfUser', {
                      initialValue: user.typeOfUser,
                      rules: [
                        {
                          required: true,
                        },
                      ],
                    })(
                      <RadioGroup disabled={userLogin.rol === 'client' || user.UserId}>
                        <Radio style={radioStyle} value={1}>
                          {messages.signup.typeOfUsers.nqnp}
                        </Radio>
                        <Radio style={radioStyle} value={2}>
                          {messages.signup.typeOfUsers.qnp}
                        </Radio>
                        <Radio style={radioStyle} value={3}>
                          {messages.signup.typeOfUsers.qp}
                        </Radio>
                      </RadioGroup>
                    )}
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </Modal>
        </div>
      </div>
    );
  }
}
const WrappedUsersCmp = Form.create()(injectIntl(UsersCmp));

export default withRouter(WrappedUsersCmp);
